body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.menu_active{

  font-weight: bold;
   font-style:inherit;
   
  border-bottom: 1px solid #52af6e;
}

#header{

  width: 100%;
  height: 80vh;
  margin-top: -5px;
}
#header h1{

  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}

#header .brand-name{
  color: #3498db;
}

#header h2{
  color: #484848;
  font-size: 24px;
}

#header .btn-get-started{
   font-weight: 500;
   font-size: 16px;
   letter-spacing: 1px;
   display: inline-block;
   padding: 10px 30px;
   border-radius: 50px;
   transition: 0.5s;
   color: #3498db;
   border: 2px solid #3498db;
   text-decoration: none;

}


@media(max-width: 991px){
          #header{

            height: 80vh;
            text-align: center;
          }
}


#header .btn-get-started:hover{
  background: #3498db;
  color: #fff;

}

.header-img{
  text-align: right;
  
   
}


#header .animated {

  animation: up-down 2s ease infinite alternate-reverse both;
}


@-webkit-keyframes up-down{

  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}
@keyframes up-down{

  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}